<template>
    <div id="app">
        <Fheader v-bind:title="header_title"></Fheader>
        <p class="text-center usertype-tip">请选择身份</p>
        <div class="usertype-form">
            <template v-for="(usertype, index) in usertype_array">
                <div class="usertype-radio" v-bind:class="[usertype.selected ? 'usertype-radio-selected' : '']" v-on:click="usertypeHandler(index)">
                    {{ usertype.name }}
                </div>
            </template>
        </div>
		<a v-bind:href="next_link" class="ft-bar-next" v-on:click="nextHandler">下一步</a>
		<transition name="fade">
			<Toast v-bind:msg="toast_text" v-if="toastshow"></Toast>
		</transition>
    </div>
</template>

<script>
import Fheader from '../../components/common/Fheader'
import Toast from '../../components/common/Toast'
import store from '../../store/store.js'
import utils from '../../utils/utils.js'

export default {
    name: 'app',
    data: function(){
    	return {
    		toastshow: false,
    		toast_text: '',
            header_title: '身份选择',
            next_link: 'javascript:void(0);',
            usertype_array: [
                {
                    name: '患者或家属',
                    selected: false
                },
                {
                    name: '医生',
                    selected: false
                }
            ]
    	}
    },
    methods:{
        usertypeHandler: function(index){
            var that = this;
            for(var i=0;i<that.usertype_array.length;i++){
                that.usertype_array[i].selected = false;
            }
            that.usertype_array[index].selected = true;
            that.next_link = index == 0 ? store.site_links.baseinfo : store.site_links.doctorinfo;
        },
    	showToast: function(){
    		var that = this;
    		if(that.toastshow){
    			return;
    		}
    		that.toastshow = true;
    		setTimeout(function(){
    			that.toastshow = false;
    		}, 3000);
    	},
        nextHandler: function(){
            var that = this;
            var data = that.getFormData();
            if(data.usertype < 0){
                that.toast_text = '请选择您的身份！';
                that.showToast();
                return;
            }
            location.href = data.usertype == 0 ? 'baseinfo.html' : 'doctorinfo.html';
        },
        getFormData: function(){
            var that = this;
            var data = {
                usertype: -1
            };
            for(var i=0;i<that.usertype_array.length;i++){
                if(that.usertype_array[i].selected){
                    data.usertype = i;
                }
            }
            return data;
        }
    },
    components: {
    	Fheader,
        Toast
    }
}
</script>

<style>
@import '../../assets/css/common.css';
.usertype-tip{
    font-weight: 600;
    color: #4d6983;
    margin: 80px 0px 30px 0px;
}
.usertype-form{
    padding: 10px 15px;
}
.usertype-radio{
    width: 100%;
    padding: 10px 20px;
    text-align: center;
    border: 1px solid #e4e4e4;
    border-radius: 5px;
    margin: 20px 0px 0px;
}
.usertype-radio-selected{
    background: #49ced4;
    color: #fff;
}
</style>



